import React from "react"
import { Link, graphql } from "gatsby"
//import Img from "gatsby-image"
import ULOCO from "../images/ulocoSquare.png"

import Layout from "../components/layout"
import SEO from "../components/seo"
import '../styles/home.scss'

// ----- For Slick Start -----
import B1 from "../images/d.png"
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
// ----- For Slick End -----

const IndexPage = ({ data, location }) => {
  const siteTitle = data.site.siteMetadata.title

  const settings = {
    dots: true,
    infinite: false,
    speed: 500,
    slidesToShow: 2,
    slidesToScroll: 1,
    //centerMode: true,
    //centerPadding: "200px",
    draggable: true,
    lazyLoad: true,
    initialSlide: 0,
    responsive: [
        {
          breakpoint: 650,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1,
            initialSlide: 1,
            centerPadding: "20px",
          }
        }
      ]
  };
  return (
    <Layout location={location} title={siteTitle}>
      <SEO title="Home"/>
      <div id="home">
        <div className="siteTitle">
          <h1>OTQ Studio</h1>
          <p>Digital Contents Studio</p>
        </div>
        <h2>News</h2>
        <Slider {...settings}>
        {data.allWpPost.edges.map(({node}) => (
            <article key={node.slug}>
              <Link to={node.slug} css={{textDecoration: `none`}}>
                <img src={node.featuredImage.node.sourceUrl} alt=""/>
                <div className="info">
                  <p>{node.title}</p>
                </div>
              </Link>
            </article>
        ))}
        <article class="more">
          <Link to="/news">
              <img src={B1} alt=""/>
              <div className="info">
                <p>More</p>
              </div>
          </Link>
        </article>
        </Slider>
        <h2>OTQ Studio create Digital Contents below.</h2>
        <ul className="projects">
          <li><Link to="/uloco"><img src={ ULOCO } alt="Free Illustrations ULOCO" /></Link></li>
          <li><div><p>Coming Soon... <span role="img" aria-label="clacker">ðŸŽ‰</span></p></div></li>
        </ul>
      </div>
    </Layout>
  )
}

export default IndexPage

export const query = graphql`
    query {
        site {
          siteMetadata {
            title
          }
        }
        allWpPost(filter: {tags: {nodes: {elemMatch: {slug: {eq: "english"}}}},categories: {nodes: {elemMatch: {slug: {eq: "otqstudio"}}}}}) {
            edges {
                node {
                    title
                    excerpt
                    slug
                    featuredImage {
                      node {
                        sourceUrl
                      }
                    }
                }
            }
        }
    }
`
